<template>
  <div class="home" :style="screenHeight">
    <div class="btn-group">
      <Input v-model="baseSearch" icon="ios-search" placeholder="请输入试点名" class="baseSearchBtn"></Input>
      <Select v-model="selectedPilot" class="pilotSelect">
        <OptionGroup :label="item.name" v-for="item in pilotData">
          <Option v-for="cItem in item.pilot" :value="cItem.uid" :key="cItem.index">{{ cItem.name }}</Option>
        </OptionGroup>
      </Select>
      <Select v-model="selectedFilter"  class="filterSelect">
        <Option v-for="item in filterList" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
      <Button type="primary" @click="addPilot">添加受试者</Button>
    </div>
    <div class="cont-list">
      <contItem v-for="item in contList" :contNumber="item.number" :contDetail="item.detail" :id="item.id"></contItem>
    </div>
    <div class="cont-page">
      <Page :total="100" show-elevator></Page>
    </div>

    <div class="dialog">
      <Modal
        v-model="dialogShow"
        title="基本信息"
        @on-ok="ok"
        @on-cancel="cancel">
        <Form :model="formItem" :label-width="80" style="padding:15px 50px 0 50px">
          <FormItem label="项目*" >
            <Select v-model="formItem.project">
              <Option value="one">项目一</Option>
              <Option value="two">项目二</Option>
              <Option value="three">项目三</Option>
            </Select>
          </FormItem>
          <FormItem label="中心*" >
            <Select v-model="formItem.pilot">
              <Option value="one">Site01-成都军区总医院</Option>
              <Option value="two">Site02-北京协和医院</Option>
              <Option value="three">Site03-上海儿童医院</Option>
            </Select>
          </FormItem>
          <FormItem label="受试者编号*">
            <Input v-model="formItem.number1" placeholder="请输入"></Input>
          </FormItem>
          <FormItem label="入组号*">
            <Input v-model="formItem.number2" placeholder="请输入"></Input>
          </FormItem>
          <FormItem label="姓名缩写*">
            <Input v-model="formItem.number3" placeholder="请输入"></Input>
          </FormItem>
          <FormItem label="受试者状态*">
            <Select v-model="formItem.status">
              <Option value="one">登记</Option>
              <Option value="two">1</Option>
              <Option value="three">2</Option>
            </Select>
          </FormItem>
        </Form>
      </Modal>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        formItem:{},
        pilotData: [
          {
            uid: '1',
            name: '项目一',
            pilot: [
              {
                name: '成都军区医院',
                uid: '1.1',
                index:'1'
              },
              {
                name: '北京协和医院',
                uid: '1.2',
                index:'2'
              }

            ]
          },
          {
            uid: '2',
            name: '项目二',
            pilot: [
              {
                name: '嘉兴第一医院',
                uid: '2.1',
                index:'3'
              },
              {
                name: '嘉兴第二医院',
                uid: '2.2',
                index:'4'
              }

            ]
          }, {
            uid: '3',
            name: '项目三',
            pilot: [
              {
                name: '武警医院',
                uid: '3.1',
                index:'5'
              },
              {
                name: '妇保院',
                uid: '3.2',
                index:'6'
              }

            ]
          },
        ],
        filterList:[
          {label:'最近更新',value:'update'}
        ],
        selectedFilter:'update',
        selectedPilot: '1.1',
        baseSearch: '',
        contList:[
          {
            number:'02-0314（v3.3）',
            detail:'目A / 成都军区总医院 / 登记',
            id:'1'
          },
          {
            number:'02-0315（v3.3）',
            detail:'目A / 成都军区总医院 / 登记',
            id:'2'
          },
          {
            number:'02-0316（v3.3）',
            detail:'目A / 成都军区总医院 / 登记',
            id:'3'
          },
          {
            number:'02-0316（v3.3）',
            detail:'目A / 成都军区总医院 / 登记',
            id:'4'
          },
          {
            number:'02-0316（v3.3）',
            detail:'目A / 成都军区总医院 / 登记',
            id:'5'
          }
        ],
        dialogShow: false,
        screenHeight: {minHeight:(document.documentElement.clientHeight-64-2)+'px'}
      }
    },
    methods:{
      //添加受试者弹框初始化
      addPilot(){
        this.dialogShow=true;
      },
      //添加受试者提交表单
      ok() {
        this.$Notice.config({
          duration: 1.5
        });
        this.$Notice.success({
          title: '添加受试者成功！',
        });
        // this.$Message.info('添加成功');
      },
      cancel() {
        this.$Notice.error({
          title: '添加受试者失败！',
        });
        // this.$Message.info('添加成功');
      },


    },
    mounted(){

      const that = this
      window.onresize = () => {
        return (() => {
          console.log(document.documentElement.clientHeight)
          that.screenHeight.minHeight= (document.documentElement.clientHeight-64-2)+'px'

        })()
      }
    },
  }
</script>

<style scoped>
  .cont-list{
    margin:0 100px;
  }
  .btn-group{
    margin-right: 100px;
    padding:30px 0;
    text-align: right;
  }
  .baseSearchBtn {
    width: 200px;
    text-align: left;
  }
  .pilotSelect{
    width: 200px;
    text-align: left;
  }
  .filterSelect{
    width: 200px;
    text-align: left;
  }
  .cont-page{
    margin:30px 0;
    text-align: center;
  }
</style>



